@import "../../common/style/sass/variables";
@import "../../common/style/sass/mixins";

.skill {
    border: 1px solid;
    background-color: $bgColor;
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    margin: 5px;
    @include text(('Poppins', sans-serif), 12px, 1.8);
    img {
        width: 50px;
        height: 50px;
        object-fit: cover;
        background: $textColor;
    }
}



